import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.min.js";
import "./App.css";
import { useState, useMemo } from "react";
import Header from "./components/Header";
import Sidebar from "./components/Sidebar";
import PrevHistSrchTable from "./components/PrevHistSrchTable";
import RightSection from "./components/RightSection";

const App = () => {
  const [clicked, setClicked] = useState("");
  const [srchInput, setSrchInput] = useState("");
  const [srchOption, setSrchOption] = useState('select');
  const [selected, setSelected] = useState(null);
  const [userName, setUserName] = useState("");

  function ajaxCall() {
    var params = "EventType=onLoad"
    var url = `/webdesktop/CustomForms/TWC_CopyProfile/TWC_CopyProfile.jsp?${params}`;
    var xhr;

    if (window.XMLHttpRequest) xhr = new XMLHttpRequest(); // Chrome/Edge
    else if (window.ActiveXObject)
      xhr = new window.ActiveXObject("Microsoft.XMLHTTP"); // IE 5 or 6

    xhr.open("GET", url, false);
    xhr.send();

    if (xhr.status === 200) {
      var ajaxResult = xhr.responseText; 
      // console.log(ajaxResult) // JSON.parse(ajaxResult)[0].title // toUse
      if (!ajaxResult) {
        ajaxResult = " ~ ~ ~ ~ ~ ~ ";
      }

      var arr = ajaxResult.split("#");

      var newans = []; // 2d
      for (let i = 0; i < arr.length; i++) {
        newans[i] = arr[i].split("~");
        if(i === 0) {
          setUserName(''+ newans[i][0]);
          newans[i].shift() // userName
        }
      }

      return newans;
    } else {
      alert("Error while handling the request.");
      return [];
    }
  }

  var prevData;
  useState(() => {
    // New way instead of useEffect
    prevData = ajaxCall();
    console.log("prevData--> ", prevData);
  }, []);

  const data = useMemo(() => prevData, []); // for PrevHistSrchTable

  return (
    <div>
      <Header userName={userName}/>
      <div id="rest">
        <div id="mainContent">
          <Sidebar
            clicked={clicked}
            setClicked={setClicked}
            srchInput={srchInput}
            srchOption={srchOption}
            setSrchInput={setSrchInput}
            setSrchOption={setSrchOption} 
          />
          <RightSection
            clicked={clicked}
            setClicked={setClicked}
            srchInput={srchInput}
            srchOption={srchOption}
            setSrchInput={setSrchInput}
            setSrchOption={setSrchOption}
            selected={selected}
            setSelected={setSelected}
          />{" "}
          {/* toDo*/}
        </div>

        <PrevHistSrchTable newans={data} />
      </div>
    </div>
  );
};

export default App;
