import "../App.css";

const Sidebar = ({
  clicked,
  setClicked,
  srchInput,
  srchOption,
  setSrchInput,
  setSrchOption,
}) => {
  const handleTextOnChange = (e) => {
    setSrchInput(e.target.value);
  };

  const handleOptionOnChange = (e) => {
    setSrchOption(e.target.value);
    setSrchInput("");
    setClicked("");
  };

  const handleOnClick = (e) => {
    if (clicked === "") {
      setClicked("abc");
    } else {
      setClicked("");
      setSrchInput("");
      setSrchOption("select");
    }
  };

  return (
    <div id="sidebar" className="doCenter">
      <select
        id="dropdown"
        className="form-select shadow"
        aria-label="Default select example"
        onChange={handleOptionOnChange}
        value={srchOption}
      >
        <option disabled selected value="select">
          Select
        </option>
        <option value="cif_id">CIF ID</option>
        <option value="tl_number">TL Number</option>
        <option value="wi_number">WorkItem Number</option>
      </select>
      <div className="form-floating mb-3 searchIP shadow">
        <input
          className="form-control"
          id="floatingInput"
          onChange={handleTextOnChange}
          value={srchInput}
          placeholder=" "
          disabled={srchOption === "select"}
        />
        <label className="labelSearchIP" htmlFor="floatingInput">
          Type here..
        </label>
      </div>

      <div>
        <button
          id="sideBarBtn"
          onClick={handleOnClick}
          type="button"
          className={`btn btn-${clicked ? "secondary" : "danger"} m-3 shadow`}
          disabled={!srchInput || !srchOption}
          style={{ transition: 'background-color 0.8s ease-in-out' }}
        >
          {clicked ? "Clear" : "Search"}
        </button>
      </div>
    </div>
  );
};

export default Sidebar;
